* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}

:root {
  --light-color: #00cc99;
  --normal-color: #009973;
  --dark-color: #00664d;
}

/* ///////////////////// navbar //////////////////////// */

.navbar {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  background-color: var(--dark-color);
  position:sticky;
  top: 0;
  box-shadow: black 0px 3px 8px;
}

.resume {
  color: black;
  text-decoration: none;
  background-color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
}

.resume:hover {
  background-color: var(--light-color);
}

@media only screen and (min-width: 600px) {
  .menu {
    color: white;
    text-decoration: none;
    padding: 5px 10px;
    border-radius: 5px;
  }

  .menu:hover {
    background-color: var(--light-color);
    color: black;
  }

  .menu-icon {
    display: none;
  }
}

@media only screen and (max-width: 600px) {
  .menu-icon {
    border: 1px solid white;
    width: 38px;
    position: fixed;
    top: 10px;
    left: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    cursor: pointer;
  }

  .menu-icon > img {
    width: 100%;
  }

  .menu-link {
    width: 150px;
    display: grid;
    position: fixed;
    top: 60px;
    left: 60px;
    background-color: var(--dark-color);
    padding: 20px;
  }

  .menu {
    color: white;
    text-decoration: none;
    padding: 5px;
    border-radius: 5px;
  }

  .menu:hover {
    background-color: var(--light-color);
    color: black;
  }
}

/* /////////////////////// home //////////////////////// */

.home {
  text-align: center;
  padding: 20px;
  margin: 100px 0px 150px 0px;
}

.greeting {
  font-weight: bold;
  margin-bottom: 20px;
}

.my-name {
  font-size: 65px;
  color: var(--normal-color);
  margin-bottom: 20px;
}

.my-segment {
  font-size: x-large;
  margin-bottom: 40px;
}

.about-me-first {
  padding: 0px 10%;
  color: var(--dark-color);
}

/* //////////////////////////// about /////////////////////// */
.about {
  margin-bottom: 150px;
}

.small-heading {
  font-weight: bold;
  text-align: center;
  margin-bottom: 20px;
}

.heading {
  text-align: center;
  font-size: xx-large;
  margin-bottom: 50px;
}

.image-sec > img {
  width: 100%;
  border-radius: 50px;
}

.links > img {
  width: 30px;
  margin-right: 10px;
  cursor: pointer;
}

@media only screen and (min-width: 600px) {
  .about-me-sec {
    display: flex;
    justify-content: space-around;
    background-color: var(--normal-color);
    padding: 5% 10%;
  }

  .about-sec {
    width: 50%;
    display: flex;
    align-items: center;
  }

  .about-para {
    margin-bottom: 20px;
    color: white;
    letter-spacing: 0.1em;
  }

  .image-sec {
    width: 25%;
  }
}

@media only screen and (max-width: 600px) {
  .about-me-sec {
    background-color: var(--normal-color);
    padding: 15% 10%;
  }

  .about-para {
    margin-bottom: 20px;
    color: white;
    letter-spacing: 0.1em;
    text-align: center;
  }

  .image-sec {
    margin-bottom: 30px;
  }

  .links {
    text-align: center;
  }
}


/* //////////////////////// skills ////////////////////////// */

.skills {
  margin-bottom: 150px;
}

.skill {
  box-shadow: black 0px 1px 4px;
  text-align: center;
  padding: 25px 0px;
  border-radius: 10px;
}

.skill > img {
  width: 30%;
}

.skill > span {
  color: var(--light-color);
  display: block;
}

@media only screen and (min-width: 600px) {
  .skills-sec {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 50px 80px;
    padding: 0px 10%;
  }
}

@media only screen and (max-width: 600px) {
  .skills-sec {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 50px;
    padding: 0px 15%;
  }
}